/*=== VARIABLES ===*/
/*============ TEXT COLOR VARIATION =============*/
@import url("https://fonts.googleapis.com/css?family=Playfair+Display:700");
@import url("https://fonts.googleapis.com/css?family=Lato:400,700");
.text-primary {
  color: #7bd1df !important;
}

/* Background Colors */
.bg-white {
  background-color: #ffffff;
}

.bg-dark {
  background-color: #323f41;
}

/*=== MEDIA QUERY ===*/
body {
  font-family: 'Lato', sans-serif;
}

/* =====================
Landing Header style
===================== */
a {
  -webkit-transition: color 0.3s ease;
  -moz-transition: color 0.3s ease;
  -ms-transition: color 0.3s ease;
  -o-transition: color 0.3s ease;
  transition: color 0.3s ease;
}

a:hover, a:focus, a:active {
  text-decoration: none;
}

.header {
  background-color: transparent;
  position: absolute;
  width: 100%;
  z-index: 555;
  padding: 0 30px;
}

.main-nav {
  padding-top: 20px;
  padding-bottom: 20px;
}

.main-nav.navbar-sticky {
  display: none;
}

@media (min-width: 768px) {
  .main-nav {
    padding: 0 !important;
  }
}

.main-nav .navbar-brand {
  padding: 0px;
  max-width: 150px;
  overflow: hidden;
}

@media (min-width: 768px) {
  .main-nav .navbar-brand {
    max-width: 230px;
  }
}

@media (min-width: 1200px) {
  .main-nav .navbar-brand {
    max-width: 280px;
    overflow: visible;
  }
}

.main-nav .navbar-brand img {
  width: 100%;
}

@media (min-width: 992px) {
  .main-nav .navbar-brand img {
    width: auto;
  }
}

.main-nav .navbar-toggler {
  -webkit-align-self: flex-start;
  -ms-flex-item-align: start;
  align-self: flex-start;
  padding: 0;
  background: transparent;
  border: none;
  cursor: pointer;
  border-radius: 0px;
  position: absolute;
  right: 15px;
  top: 20px;
  margin-top: 0px;
}

.main-nav .navbar-toggler .burger-menu {
  width: 40px;
  height: 40px;
  display: inline-block;
  font-size: 18px;
  line-height: 40px;
  color: #ffffff;
  background: #7bd1df;
  text-align: center;
  border-radius: 50%;
}

.main-nav .navbar-toggler.active, .main-nav .navbar-toggler:focus {
  outline: none;
}

.main-nav .navbar-collapse {
  padding: 0;
  border-top: none;
  max-height: 340px;
  margin-top: 10px;
}

@media (min-width: 768px) {
  .main-nav .navbar-collapse {
    margin-top: 0px;
  }
}

.main-nav .collapse.show {
  overflow-y: auto;
}

.main-nav .navbar-nav {
  position: relative;
  z-index: 555;
  background: #3c3c3c;
}

.main-nav .navbar-nav:after {
  content: "";
  display: table;
  clear: both;
}

@media (min-width: 768px) {
  .main-nav .navbar-nav {
    position: static;
    background: transparent;
  }
}

.main-nav .nav-item a {
  text-transform: capitalize;
}

.main-nav .nav-item.dropdown > a:hover:before, .main-nav .nav-item.dropdown > a:focus:before {
  opacity: 1;
}

.main-nav .nav-item a.nav-link {
  color: #ffffff;
  padding: 0px;
  font-size: 14px;
  position: relative;
  text-transform: uppercase;
  font-weight: 700;
  display: block;
  margin: 10px 15px;
}

@media (min-width: 768px) {
  .main-nav .nav-item a.nav-link {
    color: #ffffff;
    padding: 40px 6px;
    margin: 0px;
    font-size: 13px;
  }
}

@media (min-width: 992px) {
  .main-nav .nav-item a.nav-link {
    padding: 40px 20px;
    margin: 0px;
    font-size: 14px;
  }
}

.main-nav .nav-item a.nav-link:before {
  content: '';
  position: absolute;
  width: 100%;
  top: 40px;
  left: 0;
  opacity: 0;
  -webkit-transition: opacity 0.3s ease-in-out;
  -moz-transition: opacity 0.3s ease-in-out;
  -ms-transition: opacity 0.3s ease-in-out;
  -o-transition: opacity 0.3s ease-in-out;
  transition: opacity 0.3s ease-in-out;
}

@media (min-width: 768px) {
  .main-nav .nav-item a.nav-link:before {
    top: 70px;
    border-bottom: 2px solid #ffffff;
  }
}

.main-nav .nav-item.dropdown > a.nav-link:after {
  position: absolute;
  border: none;
  top: 5px;
  opacity: 1;
  content: "\f107";
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  right: 8px;
}

@media (min-width: 768px) {
  .main-nav .nav-item.dropdown > a.nav-link:after {
    opacity: 0;
  }
}

.show > a.nav-link {
  color: #ffffff;
}

.show > a.nav-link:hover, .show > a.nav-link:focus {
  color: #ffffff;
}

.show > a:after {
  content: "\f106";
}

.show > .dropdown-menu {
  -webkit-transition: transform 0.3s, opacity 0.3s ease-in-out;
  -moz-transition: transform 0.3s, opacity 0.3s ease-in-out;
  -ms-transition: transform 0.3s, opacity 0.3s ease-in-out;
  -o-transition: transform 0.3s, opacity 0.3s ease-in-out;
  transition: transform 0.3s, opacity 0.3s ease-in-out;
  -moz-transform: scale(1, 1);
  -o-transform: scale(1, 1);
  -ms-transform: scale(1, 1);
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
  opacity: 1;
}

.dropdown-menu {
  top: 100%;
  min-width: 230px;
  margin-top: 0px;
  width: auto;
  padding: 0px 30px 10px;
  border-radius: 0px;
  background-color: rgba(255, 255, 255, 0.1);
  -webkit-box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.08);
  -moz-box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.08);
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.08);
}

.dropdown-menu a {
  padding: 10px 0;
  display: block;
  color: #ffffff;
}

@media (min-width: 768px) {
  .dropdown-menu a {
    color: #848484;
  }
  .dropdown-menu a:hover {
    color: #222222;
  }
}

@media (min-width: 768px) {
  .dropdown-menu {
    -webkit-transform-origin: top;
    transform-origin: top;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    -moz-transform: scale(1, 0);
    -o-transform: scale(1, 0);
    -ms-transform: scale(1, 0);
    -webkit-transform: scale(1, 0);
    transform: scale(1, 0);
    background-color: #ffffff;
    margin-top: -25px;
    padding: 20px;
    display: block;
    opacity: 0;
    -webkit-transition: transform 0.3s, opacity 0.3s ease-in-out;
    -moz-transition: transform 0.3s, opacity 0.3s ease-in-out;
    -ms-transition: transform 0.3s, opacity 0.3s ease-in-out;
    -o-transition: transform 0.3s, opacity 0.3s ease-in-out;
    transition: transform 0.3s, opacity 0.3s ease-in-out;
  }
}

@media (min-width: 992px) {
  .dropdown-menu {
    margin-top: -20px;
    padding: 20px 30px;
  }
}

.dropdown-menu li {
  padding: 0;
  border-bottom: 1px solid #e5e5e5;
}

.dropdown-menu li:last-child {
  border-bottom: 0px;
}

.dropdown-menu li.dropdown > a:after {
  position: absolute;
  border: none;
  top: 14px;
  right: 10px;
  opacity: 1;
  content: "\f107";
  display: inline-block;
  font: normal normal normal 14px/1 FontAwesome;
  font-size: inherit;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
}

@media (min-width: 768px) {
  .dropdown-menu li.dropdown > a:after {
    content: "\f105";
  }
}

.dropdown-menu li.dropdown.show > a:after {
  content: "\f106";
}

@media (min-width: 768px) {
  .dropdown-menu li.dropdown.show > a:after {
    content: "\f104";
  }
}

.dropdown-menu li.dropdown.show > .dropdown-menu {
  -webkit-transition: transform 0.3s, opacity 0.3s ease-in-out;
  -moz-transition: transform 0.3s, opacity 0.3s ease-in-out;
  -ms-transition: transform 0.3s, opacity 0.3s ease-in-out;
  -o-transition: transform 0.3s, opacity 0.3s ease-in-out;
  transition: transform 0.3s, opacity 0.3s ease-in-out;
  -moz-transform: scale(1, 1);
  -o-transform: scale(1, 1);
  -ms-transform: scale(1, 1);
  -webkit-transform: scale(1, 1);
  transform: scale(1, 1);
  opacity: 1;
  display: block;
}

@media (min-width: 768px) {
  .megaDropMenu {
    position: static !important;
  }
  .megaDropMenu .dropdown-menu {
    width: 100%;
    top: 100%;
    padding: 0 10px !important;
  }
  .megaDropMenu .dropdown-menu > li {
    float: left;
    padding: 20px !important;
    border-bottom: 0px;
  }
  .megaDropMenu .dropdown-menu ul li {
    border-bottom: 1px solid #e5e5e5;
  }
  .megaDropMenu .dropdown-menu ul li:last-child {
    border-bottom: none;
  }
}

.option-switcher-btn {
  display: none;
}

.btn-purchase2 {
  background-color: #7bd1df;
  max-width: 246px;
  margin: 25px auto;
  border-radius: 0;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 2px;
  display: block;
  padding: 0px 30px;
  color: #ffffff;
  text-transform: uppercase;
  height: 50px;
  line-height: 50px;
  text-transform: uppercase !important;
}

.btn-purchase2:hover, .btn-purchase2:focus {
  color: #ffffff;
}

@media (min-width: 768px) {
  .btn-purchase2 {
    display: inline-block;
    font-size: 13px;
    padding: 0 20px;
  }
}

@media (min-width: 992px) {
  .btn-purchase2 {
    font-size: 14px;
    padding: 0px 30px;
  }
}

/* =====================
Landing Banner style
===================== */
.landing-banner {
  background-position: center;
  background-size: cover;
  padding-top: 80px;
  padding-bottom: 80px;
  position: relative;
  height: 450px;
}

@media (min-width: 768px) {
  .landing-banner {
    height: 706px;
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.landing-banner .banner-content h1 {
  font-family: 'Playfair Display', serif;
  color: #ffffff;
  font-size: 30px;
  font-weight: 700;
  margin-top: 50px;
  margin-bottom: 25px;
  line-height: 40px;
}

@media (min-width: 768px) {
  .landing-banner .banner-content h1 {
    font-size: 36px;
    margin-top: 80px;
    margin-bottom: 25px;
    line-height: 40px;
  }
}

@media (min-width: 992px) {
  .landing-banner .banner-content h1 {
    font-size: 58px;
    margin-top: 100px;
    line-height: 55px;
  }
}

.landing-banner .banner-content p {
  color: #ffffff;
  font-size: 18px;
  line-height: 24px;
  padding: 0 5px;
}

@media (min-width: 768px) {
  .landing-banner .banner-content p {
    font-size: 21px;
    line-height: 30px;
    padding: 0 70px;
  }
}

/* =====================
Preview Area style
===================== */
.preview-area {
  padding-bottom: 80px;
  position: relative;
}

@media (min-width: 992px) {
  .preview-area {
    padding-bottom: 100px;
  }
}

.preview-area .home-preview {
  margin-top: 30px;
  overflow: hidden;
  height: 300px;
  background-position: top center;
  -webkit-box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.2);
  -moz-box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.2);
  box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.2);
  -webkit-transition: background-position 1s ease-in-out;
  -moz-transition: background-position 1s ease-in-out;
  -ms-transition: background-position 1s ease-in-out;
  -o-transition: background-position 1s ease-in-out;
  transition: background-position 1s ease-in-out;
}

@media (min-width: 768px) {
  .preview-area .home-preview {
    height: 491px;
    margin-top: -245px;
  }
}

.preview-area .home-preview:hover {
  background-position: 50% 100%;
}

p.page-title {
  color: #545454;
  text-align: center;
  font-size: 17px;
  margin-top: 25px;
}

/* =====================
Landing Footer style
===================== */
.landing-cta {
  background-position: center;
  background-size: cover;
  padding-top: 80px;
  padding-bottom: 80px;
  position: relative;
}

.landing-cta:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(41, 45, 58, 0.9);
}

@media (min-width: 992px) {
  .landing-cta {
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.landing-cta .cta-purchase {
  position: relative;
  z-index: 55;
}

.landing-cta .cta-purchase h1 {
  font-family: 'Playfair Display', serif;
  font-size: 30px;
  line-height: 36px;
  margin-bottom: 25px;
  color: #ffffff;
}

@media (min-width: 768px) {
  .landing-cta .cta-purchase h1 {
    font-size: 36px;
    line-height: 44px;
    margin-bottom: 30px;
  }
}

@media (min-width: 992px) {
  .landing-cta .cta-purchase h1 {
    font-size: 50px;
    line-height: 44px;
    margin-bottom: 50px;
  }
}

.landing-cta .cta-purchase p {
  color: #ffffff;
  font-size: 18px;
  line-height: 24px;
  padding: 0 5px;
}

@media (min-width: 768px) {
  .landing-cta .cta-purchase p {
    font-size: 20px;
    line-height: 26px;
    padding-bottom: 45px;
  }
}

.landing-cta .cta-purchase a.btn {
  font-weight: 700;
}

footer.footer-landing {
  background-color: #f5f5f5;
  padding-top: 0px;
  padding-bottom: 0px;
}

footer.footer-landing .copyRight_text {
  padding-top: 20px;
  padding-bottom: 20px;
}

footer.footer-landing .copyRight_text p {
  margin: 0px;
}

footer.footer-landing .copyRight_text p a:hover {
  color: #7bd1df !important;
}
